<template>
    <div class="basics-task">
        <adt-search-folder
            :searchRouter="{query: {accountName: search.accountName, organizationId: search.organizationId, phoneNumber: search.phoneNumber}}"
            @on-triggerFolder="searchChange" :isStatus="true">
            <template slot="status">
                <div class="status">
                    <Col span="10">
                        <div class="name">审核状态:</div>
                        <span v-for="(status, id) in auditStatusList" :key="id" @click="auditStatus(id)" :class="{'click-status': auditStatusNum == id}">
                            {{status.status}}
                        </span>
                    </Col>
                    <Col span="14">
                        <div class="name">启用状态:</div>
                        <span v-for="(status, id) in enableStatusList" :key="id" @click="enableStatus(id)" :class="{'click-status': enableStatusNum == id}">
                            {{status.status}}
                        </span>
                    </Col>
                </div>
            </template>
            <template slot="always-show">
                <Col span="5" class="search-condition">
                    <span> 基础任务编号：</span>
                    <Input v-model="search.accountName" clearable></Input>
                </Col>
                <Col span="5" class="search-condition line-margin">
                    <span> 基础任务名称：</span>
                    <Input v-model="search.organizationId" clearable></Input>
                </Col>
                <Col span="5" class="search-condition line-margin">
                    <span> 运输类型：</span>
                    <Select v-model="basicTaskData.phoneNumber" clearable>
                        <Option v-for="(item, index) in basicTaskClassifyList" :key="index" :value="item.value">{{ item.label }}</Option>
                    </Select>
                </Col>
            </template>
        </adt-search-folder>
        <div class="btns-paginator">
            <AdtButton>
                <Button @click="add()">新增基础任务<Icon type="ios-add-circle-outline" /></Button>
            </AdtButton>
            <adt-paginator @getPages="getPages" class="pagination" :currentPage="~~page" :total="~~total" path="/authority-manage/user-manage" />
        </div>
        <AdtMyModal
            :title="modalTitle"
            okText="提交审核"
            cancelText="取消"
            :showModal="isModalShow"
            @on-visible-change="addvisibleChange"
            @on-ok="save()"
            @on-cancel="cancel()"
            width="650">
            <Form
                ref="formValidate"
                inline
                :model="basicTaskData"
                :label-colon="true"
                label-position="left"
                :label-width="120"
                class="two-column">
                <FormItem label="基础任务编号" style="width: 100%;">
                    {{ 'GDF534534' }}
                </FormItem>
                <FormItem label="基础任务名称" prop="organizationId">
                    <Input v-model="basicTaskData.organizationId" />
                </FormItem>
                <FormItem label="基础任务分类" prop="phoneNumber">
                    <Select v-model="basicTaskData.phoneNumber" clearable>
                        <Option v-for="(item, index) in basicTaskClassifyList" :key="index" :value="item.value">{{ item.label }}</Option>
                    </Select>
                </FormItem>
                <hr class="line">
                <div class="modal-table">
                    <p>选择数据项</p>
                    <Table :columns="modalTable" :data="tableData" stripe size="small" height="300"></Table>
                </div>
            </Form>
        </AdtMyModal>
        <Table :columns="tableColumns" :data="tableData" stripe>
            <!-- 审核状态 -->
            <template slot-scope="{ row, index }" slot="auditStatus">
                <span :class="{success: row.statusCode == 'pass', failure: row.statusCode == 'failed'}">{{row.status}}</span>
            </template>
            <!-- 启用状态 -->
            <template slot-scope="{ row, index }" slot="enableStatus">
            </template>
            <!-- 操作 -->
            <template slot-scope="{ row, index }" slot="action">
                <Icon class="icon-action" type="ios-contact-outline" @click="audit(row)" />
                <Icon class="icon-action" type="md-create" @click="edit(row)" />
                <Icon class="icon-action" type="md-close" @click="remove(row)" />
                <Icon class="icon-action" type="ios-alert-outline" @click="detail(row)" />
            </template>
        </Table>
        <AdtMyModal
            title="审核编辑基础任务"
            okText="同意"
            cancelText="不同意"
            :showModal="isAuditShow"
            @on-visible-change="auditvisibleChange"
            @on-ok="save()"
            @on-cancel="cancel()"
            width="650">
            <div class="public-detail modal-title">
                <div class="important-info">
                    <Row>
                        <Col span="10">
                            <div class="name">基础任务编号：</div>
                            <div class="content">裂开了裂开</div>
                        </Col>
                        <Col span="7">
                            <div class="name">修改人：</div>
                            <div class="content">F545</div>
                        </Col>
                        <Col span="7">
                            <div class="name">修改时间：</div>
                            <div class="content">裂开了了</div>
                        </Col>
                    </Row>
                </div>
                <hr class="line">
                <div class="audit-detail">
                    <p class="list">
                        <span class="content-tit">修改基础任务名称：</span>
                        <ul>
                            <li><i class="ico"></i>运输任务</li>
                        </ul>
                        <span><i class="ico ico-blue"></i>物流运输任务</span>
                        <div class="full-line"></div>
                    </p>
                    <p class="list">
                        <span class="content-tit">删除数据项：</span>
                        <ul class="margin">
                            <li>运输任务</li>
                            <li>运输任务</li>
                            <li>运输任务</li>
                        </ul>
                        <div class="full-line"></div>
                    </p>
                    <p class="list">
                        <span class="content-tit">新增数据项：</span>
                        <ul class="margin">
                            <li>通知企业</li>
                        </ul>
                    </p>
                </div>
            </div>
        </AdtMyModal>
        <adt-reconfirm-modal :showModal="isActionDel" @on-visible-change="visibleChange" @on-ok="allDelClick()" />
    </div>
</template>

<script>
import paginator from '@/mixins/paginator'
export default {
    mixins: [paginator],
    data() {
        return {
            modalTitle: '新增基础任务',
            isModalShow: false,
            isActionDel: false,
            isAuditShow: false,
            search: {},
            basicTaskData: {},
            basicTaskClassifyList: [
                {
                    label: '运输',
                    value: 'ys'
                },
                {
                    label: '场所',
                    value: 'cs'
                }
            ],
            auditStatusNum: '默认值',
            enableStatusNum: '默认值',
            auditStatusList: [
                {code: 'unreviewed', status: '未审核'},
                {code: 'stock', status: '未货好'},
                {code: 'progress', status: '执行中'}
            ],
            enableStatusList: [
                {code: 'unreviewed', status: '已启用'},
                {code: 'stock', status: '未启用'},
                {code: 'progress', status: '已启用'}
            ],
            modalTable: [
                {
                    type: 'selection',
                    width: 60,
                    align: 'center'
                },
                {
                    title: '基础任务编号',
                    align: 'center',
                    key: 'accountName'
                },
                {
                    title: '基础任务名称',
                    align: 'center',
                    key: 'organizationId'
                }
            ],
            tableColumns: [
                {
                    title: '基础任务编号',
                    align: 'center',
                    key: 'accountName',
                    tooltip: true
                },
                {
                    title: '基础任务名称',
                    align: 'center',
                    key: 'organizationId'
                },
                {
                    title: '物流类型',
                    align: 'center',
                    key: 'phoneNumber'
                },
                {
                    title: '运输类型',
                    align: 'center',
                    key: 'phoneNumber'
                },
                {
                    title: '创建人',
                    align: 'center',
                    key: 'phoneNumber'
                },
                {
                    title: '创建时间',
                    align: 'center',
                    key: 'phoneNumber'
                },
                {
                    title: '修改人',
                    align: 'center',
                    key: 'phoneNumber'
                },
                {
                    title: '修改时间',
                    align: 'center',
                    key: 'phoneNumber'
                },
                {
                    title: '审核人',
                    align: 'center',
                    key: 'phoneNumber'
                },
                {
                    title: '审核状态',
                    align: 'center',
                    slot: 'auditStatus'
                },
                {
                    title: '审核时间',
                    align: 'center',
                    key: 'phoneNumber'
                },
                {
                    title: '备注',
                    align: 'center',
                    slot: 'enableStatus'
                },
                {
                    title: '操作',
                    align: 'center',
                    key: 'operating',
                    slot: 'action',
                    width: 150
                }
            ],
            tableData: [
                {accountName: '122222331234564324561', organizationId: '22', phoneNumber: '33', accountEmail: '44', status:'审核通过', statusCode: 'pass'},
                {accountName: '11', organizationId: '22', phoneNumber: '33', accountEmail: '44', status:'审核失败', statusCode: 'failed'},
                {accountName: '11', organizationId: '22', phoneNumber: '33', accountEmail: '44', status:'审核失败', statusCode: 'unreviewed'},
                {accountName: '11', organizationId: '22', phoneNumber: '33', accountEmail: '44', status:'审核通过', statusCode: 'pass'},
                {accountName: '11', organizationId: '22', phoneNumber: '33', accountEmail: '44', status:'审核失败', statusCode: 'failed'}
            ]
        }
    },
    methods: {
        getPages (page) {
            this.page = page
        },
        addvisibleChange (changeStatus) {
            this.isModalShow = changeStatus
        },
        auditvisibleChange (changeStatus) {
            this.isAuditShow = changeStatus
        },
        visibleChange (changeStatus) {
            this.isActionDel = changeStatus
        },
        dataInit () {
            this.getData()
        },
        getData () {},
        add () {
            this.isModalShow = true
        },
        searchChange () {},
        audit () {
            this.isAuditShow = true
        },
        // 编辑
        edit () {
            this.isModalShow = true
        },
        // 删除
        remove () {
            this.isActionDel = true
        },
        allDelClick () {},
        save () {},
        cancel () {},
        detail (row) {
            this.$router.push({path: '/standard-mapper/basics-task/detail'})
        },
        auditStatus (index) {
            // 控制状态切换传值
            this.auditStatusNum = index
        },
        enableStatus (index) {
            // 控制状态切换传值
            this.enableStatusNum = index
        }
    },
}
</script>

<style lang="scss" scoped>
span{
    width: 120px;
    line-height: 30px;
}
.status p{
    display: inline-block;
    font-size: $font-size-large;
    font-weight: bolder;
    font-family: $base-font-family;
}
.search-condition {
    span{
        width: 200px!important;
    }
}
.ivu-modal-body{
    .search-condition{
        span{
            width: 180px!important;
        }
    }
}
.line{
    border:0.5px dashed $border-color;
    margin: 20px 0;
}
// 新增下的列表
.table {
    display: flex;
    p{
        margin: 5px 0;
        font-size: $font-size-base;
    }
    .table-left{
        margin: 0 20px;
    }
}
.search-condition{
    display: flex;
}
.modal-code{
    font-size: 14px;
    font-weight: 400;
    span{
        margin-left: 10px;
    }
}
.line-margin {
    margin-left: 30px;
}
.modal-css{
    margin-left: 0;
}
.icon-action{
    margin: 0 4px;
}
</style>
